    font-size: 14px;
    font-size: 0.875rem;
  }
}
@media only screen and (min-width: 1170px) {
  .newTimeLine .cd-timeline-content {
    margin-left: 0;
    padding: 1.6em;
    width: 45%;
  }
  .newTimeLine .cd-timeline-content::before {
    top: 24px;
    left: 100%;
