    /*border-left-color: white;*/
    border-left-color: transparent;
  }
  .newTimeLine .cd-timeline-content .cd-read-more {
    float: left;
  }
  .newTimeLine .cd-timeline-content .cd-date {
    position: absolute;
    width: 100%;
    left: 122%;
    top: 6px;
    font-size: 16px;
    font-size: 1rem;
  }
  .newTimeLine .cd-timeline-block:nth-child(even) .cd-timeline-content {
    float: right;
  }
  .newTimeLine .cd-timeline-block:nth-child(even) .cd-timeline-content::before {
    top: 24px;
    left: auto;
    right: 100%;
    border-color: transparent;
    border-right-color: white;
  }
  .newTimeLine .cd-timeline-block:nth-child(even) .cd-timeline-content .cd-read-more {
    float: right;
  }
  .newTimeLine .cd-timeline-block:nth-child(even) .cd-timeline-content .cd-date {
    left: auto;
    right: 122%;
    text-align: right;
  }
  .newTimeLine .cssanimations .cd-timeline-content.is-hidden {
    visibility: hidden;
  }
  .newTimeLine .cssanimations .cd-timeline-content.bounce-in {
    visibility: visible;
    -webkit-animation: cd-bounce-2 0.6s;
    -moz-animation: cd-bounce-2 0.6s;
    animation: cd-bounce-2 0.6s;
  }
}
@media only screen and (min-width: 1170px) {
  .newTimeLine {
