.newTbList {
  margin: 0 auto;
}
.newTbList .theBookBox {
  padding: 20px;
}
.newTbList .bookBox {
  cursor: pointer;
  display: flex;
  position: relative;
  -webkit-transform-style: preserve-3d;
  -moz-transform-style: preserve-3d;
  transform-style: preserve-3d;
  -webkit-transition: -webkit-transform 0.5s;
  -moz-transition: -moz-transform 0.5s;
  transition: transform .5s;
}
.newTbList .bookBox:hover {
  -webkit-transform: rotate3d(0, 1, 0, 35deg);
  -moz-transform: rotate3d(0, 1, 0, 35deg);
  transform: rotate3d(0, 1, 0, 35deg);
}
.newTbList .bookBox:hover .bookCover:before {
  left: 20px;
}
.newTbList .bookBox_shaw {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  -webkit-transform: rotate3d(0, 1, 0, -180deg) translate3d(0, 0, 20px);
  -moz-transform: rotate3d(0, 1, 0, -180deg) translate3d(0, 0, 20px);
  transform: rotate3d(0, 1, 0, -180deg) translate3d(0, 0, 20px);
  box-shadow: 10px 10px 30px rgba(0, 0, 0, 0.3);
  border-radius: 3px 0 0 3px;
}
.newTbList .bookCover {
  background-color: #e6e8e7;
  width: 100%;
  height: 351px;
  position: relative;
}
.newTbList .bookCover:before {
  content: '';
  position: absolute;
  top: 0;
  left: 10px;
  bottom: 0;
  width: 3px;
  background: rgba(0, 0, 0, 0.06);
  box-shadow: 1px 0 3px rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease-in-out;
}
.newTbList .bookTopImg {
  width: 150px;
  height: 150px;
  margin: 20px auto;
  overflow: hidden;
  border-radius: 50%;
  border: 5px solid #f2f2f2;
}
.newTbList .bookTopImg img {
  max-width: 100%;
}
.newTbList .BookbottomTextBox {
  width: 80%;
  text-align: center;
  margin: 40px auto 20px auto;
}
.newTbList .BookbottomTextBox .topTextBox {
  font-size: 12px;
  margin: 0 0 10px 0;
}
.newTbList .BookbottomTextBox .bookNameTitle {
  font-weight: 900;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.newTbList .BookbottomTextBox .bookauthor {
  margin: 10px 0;
  font-size: 12px;
  color: #bbb;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.newTbList .bookLeftside {
  width: 40px;
  left: -20px;
  height: 351px;
  position: absolute;
  background-color: #dadddd;
  -webkit-transform: rotate3d(0, 1, 0, -90deg);
  -moz-transform: rotate3d(0, 1, 0, -90deg);
  transform: rotate3d(0, 1, 0, -90deg);
  transition: transform .5s;
}
.newTbList .bookLeftside .bookTitle {
  width: 351px;
  height: 40px;
  font-size: 15px;
  line-height: 40px;
  padding-right: 10px;
  text-align: right;
  background-color: #dadddd;
  -webkit-transform-origin: 0 0;
  -moz-transform-origin: 0 0;
  transform-origin: 0 0;
  -webkit-transform: rotate(90deg) translateY(-40px);
  -moz-transform: rotate(90deg) translateY(-40px);
  transform: rotate(90deg) translateY(-40px);
}



